@use '../utilities/variables';

.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

    width: variables.$width-prod-cart;
    height: variables.$height-prod-cart;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;
    transition: color, transform, variables.$transition-time;

    &:not(:nth-child(4n)) {
      margin-right: 48px;
    }

    &:hover {
      transform: scale(120%);
      color: #34568b;
    }
  }
}
